import { LightArea } from "./LightArea.js";
import { LightShadow } from "./LightShadow.js";
export class Light {
    constructor() {
        this.area = new LightArea();
        this.shadow = new LightShadow();
    }
    load(data) {
        if (!data) {
            return;
        }
        this.area.load(data.area);
        this.shadow.load(data.shadow);
    }
}
